body {
  	margin: 0;
 	padding: 0;
	background: -webkit-linear-gradient(bottom, rgba(85,183,245,1) 21%, rgba(42,103,190,1) 58%)no-repeat fixed;
	background: linear-gradient(to top, rgba(85,183,245,1) 21%, rgba(42,103,190,1) 58%)no-repeat fixed;
	background-repeat:no-repeat;
  	font-family: Helvetica, sans-serif;
}


.App {
  text-align: center;
  display:flex-column;
  -webkit-justify-content: center;
          justify-content: center;
}

.imageForm{
	width:700px;
}

button{
	cursor:pointer;
}

.box{
	position:absolute;
	box-shadow:0 0 0 3px #149df2 inset;
	display:-webkit-flex;
	display:flex;
	-webkit-flex-wrap:wrap;
	        flex-wrap:wrap;
	-webkit-justify-content: center;
	        justify-content: center;
	cursor:pointer;
}

.center{
	display:-webkit-flex;
	display:flex;
	-webkit-justify-content: center;
	        justify-content: center;
}
